import { useCallback, useMemo } from 'react';
import { usePreloadedQuery } from 'react-relay';

import { MPDialog, MPFonts, useIsMobile } from '@mp-frontend/core-components';
import { joinClasses } from '@mp-frontend/core-utils';

import DropQueryRequest, {
  DropQuery,
} from 'graphql/__generated__/DropQuery.graphql';

import FullArtist from 'components/accounts/FullArtist';
import CSSGlobal from 'types/enums/css/Global';
import useDropPageGTM from 'utils/GTM/drop';

import { DropVideoPlayerStatus } from '../VideoPlayer/PlayerControls';
import DropVideoSection from '../VideoSection';
import Artists from './Artists';
import Links from './Links';
import Section from './Section';

import * as styles from 'css/pages/drops/components/MoreInfoDialog/index.module.css';

export default function MoreInfoDialog({ queryRef, state }) {
  const drop = usePreloadedQuery<DropQuery>(DropQueryRequest, queryRef).drops
    .edges[0].node;
  const track = useDropPageGTM();

  const [isOpen, setIsOpen] = state;
  const isMobile = useIsMobile();
  const sx = useMemo(
    () => ({
      '& .MuiDialog-paper': {
        maxWidth: isMobile ? '100%' : '506px',
      },
    }),
    [isMobile]
  );

  const onControlsClick = useCallback(
    (action: DropVideoPlayerStatus) => {
      track.trackClickDropMoreInfoModalVideoPlayer(action);
    },
    [track]
  );

  return (
    <MPDialog
      open={isOpen}
      sx={sx}
      onClose={() => setIsOpen(false)}
      title="More About This Exhibition"
    >
      <div className={joinClasses(styles.dialogContainer, CSSGlobal.Flex.Col)}>
        <Section title={drop.dropTitle}>
          <DropVideoSection
            source={drop.heroVideoUrl}
            title={drop.dropTitle}
            cover
            onControlsClick={onControlsClick}
          />
          <div
            className={joinClasses(MPFonts.paragraphNormal, styles.description)}
          >
            {drop.moreAboutDescription}
          </div>
        </Section>
        <Artists artists={drop.creators} />
        {!!drop.curator && (
          <Section title="Presented By">
            <FullArtist artist={drop.curator} hideSales />
          </Section>
        )}
        <Links links={drop.moreAboutLinks} />
      </div>
    </MPDialog>
  );
}
