import { useCallback, useMemo } from 'react';
import { usePreloadedQuery } from 'react-relay';

import { MPBackgroundColorClass } from '@mp-frontend/core-components';
import { joinClasses } from '@mp-frontend/core-utils';

import NFTsByCollection, {
  NFTsByCollectionQuery,
} from 'graphql/__generated__/NFTsByCollectionQuery.graphql';

import ArtworkCard from 'components/cards/ArtworkCard';
import Carousel from 'components/carousel/Carousel';
import ROUTES from 'constants/Routes';
import GTM from 'GTM';
import withLoadQuery, { WithLoadQueryProps } from 'utils/hocs/withLoadQuery';

import PageSection from './PageSection';

import * as styles from 'css/pages/drops/components/GenerativeArtworks.module.css';
import * as cardStyles from 'css/pages/homepage/HomepageStandardCardsSection.module.css';

interface GenerativeArtworksProps {
  collectionSlug: string;
  dropCollectionNFTsQuery: WithLoadQueryProps<NFTsByCollectionQuery>;
}

const CAROUSEL_NAME = 'GenerativeArtworks';

function GenerativeArtworks({
  dropCollectionNFTsQuery,
  collectionSlug,
}: GenerativeArtworksProps) {
  const { nfts } = usePreloadedQuery<NFTsByCollectionQuery>(
    NFTsByCollection,
    dropCollectionNFTsQuery.queryRef
  );

  const generativeNFTs = useMemo(
    () => nfts.edges.map((edge) => edge.node),
    [nfts.edges]
  );

  const logViewAbleCardsToGA = useCallback(
    (start: number, end: number) => {
      GTM.ecommerce.trackViewItemList(
        generativeNFTs.slice(start, end),
        CAROUSEL_NAME
      );
    },
    [generativeNFTs]
  );

  return (
    !!generativeNFTs.length && (
      <PageSection
        className={joinClasses(
          MPBackgroundColorClass.CommonWhite,
          styles.generativeSection
        )}
      >
        <Carousel
          header={<>Collection Activity</>}
          viewAllLink={ROUTES.PROFILE.SHOP(collectionSlug)}
          items={generativeNFTs.map((nft, idx) => (
            <ArtworkCard
              key={nft.id}
              nft={nft}
              className={joinClasses(
                cardStyles.card,
                MPBackgroundColorClass.BackgroundDefault,
                {
                  [cardStyles.last]: idx === generativeNFTs.length - 1,
                }
              )}
            />
          ))}
          containerName={CAROUSEL_NAME}
          logViewAbleCardsToGA={logViewAbleCardsToGA}
        />
      </PageSection>
    )
  );
}

export default withLoadQuery(GenerativeArtworks, {
  dropCollectionNFTsQuery: {
    concreteRequest: NFTsByCollection,
  },
});
