import ROUTES from 'constants/Routes';
import ProductPageCardLayout from 'pages/product/card/Layout';
import ProductCardTopSection from 'pages/product/card/ProductCardTopSection';
import { ProductDescriptions } from 'pages/product/ProductDescription';
import { DropSectionSubSectionItemsType } from 'types/graphql/Drop';
import { NFTDropMetadataType, NFTType } from 'types/graphql/NFT';
import {
  OpenEditionDropMetadataType,
  OpenEditionType,
} from 'types/graphql/OpenEdition';

import CollectionCoreActions from './CollectionCoreActions';
import CollectionCoreLabels from './CollectionCoreLabels';

export type CollectionCorePageCardMetadataType = Pick<
  NFTType['metadata'],
  | 'author'
  | 'collaborators'
  | 'description'
  | 'descriptionImageUrl'
  | 'descriptionVideoUrl'
  | 'hasVideo'
  | 'highResImage'
  | 'id'
  | 'nftAdditionalMediasRanked'
  | 'pk'
  | 'rawfileExtension'
  | 'standardImage'
  | 'title'
  | 'thumbnailImage'
  | 'videoUrl'
  | 'totalSupply'
> & {
  dropMetadata: Pick<NFTDropMetadataType, 'dropsAt' | 'endsAt'> & {
    drop: Pick<OpenEditionDropMetadataType['drop'], 'artworksAvailable'>;
  };
  nftCollection: Pick<
    DropSectionSubSectionItemsType['nftCollection'],
    'collectionName' | 'slug' | 'totalSupply' | 'soldCount'
  >;
  collectorReward?: NFTType['metadata']['collectorReward'];
  isSoldOut?: DropSectionSubSectionItemsType['isSoldOut'];
  openEdition?: Pick<
    OpenEditionType,
    'pk' | 'editionsSold' | 'priceInEther' | 'priceInUsd'
  >;
};

interface CollectionCorePageCardProps {
  invalidate: () => void;
  nftMetadata: CollectionCorePageCardMetadataType;
}

function CollectionCorePageCard({
  nftMetadata,
  invalidate,
}: CollectionCorePageCardProps) {
  const dropMetadata = nftMetadata?.dropMetadata;
  const openEdition = nftMetadata?.openEdition;
  const { dropsAt, endsAt } = dropMetadata;
  const editionsSold = nftMetadata.nftCollection.soldCount;
  const { totalSupply, slug } = nftMetadata.nftCollection;

  return (
    <ProductPageCardLayout
      nftMetadata={nftMetadata}
      topSection={
        <ProductCardTopSection
          link={ROUTES.COLLECTION_DROP(nftMetadata.nftCollection.slug)}
          nftMetadata={nftMetadata}
        />
      }
    >
      <ProductDescriptions
        artwork={{
          description: nftMetadata.description,
          videoPreviewImageUrl: nftMetadata.descriptionImageUrl,
          videoUrl: nftMetadata.descriptionVideoUrl,
        }}
      />
      <CollectionCoreLabels
        dropsAt={dropsAt}
        endsAt={endsAt}
        itemsSold={editionsSold}
        totalSupply={totalSupply}
        openEdition={openEdition}
        forceUpdate={invalidate}
      />
      <CollectionCoreActions
        dropMetadata={dropMetadata}
        dropsAt={dropsAt}
        endsAt={endsAt}
        isSoldOut={nftMetadata.isSoldOut}
        slug={slug}
      />
    </ProductPageCardLayout>
  );
}

export default CollectionCorePageCard;
