import { useCallback } from 'react';
import { BigNumber } from 'ethers';
import { useReadContract } from 'wagmi';

import { WagmiWriteContractManagerProps } from 'types/WagmiWriteContractProps';
import { ContractActionObject, HexString } from 'utils/jwt/walletUtils';

import useCreateContractActionObject from './useCreateContractActionObject';
import useDefaultContractOverrides from './useDefaultContractOverrides';

export default function useGmStudioContract({
  abi,
  contractAddress,
}: WagmiWriteContractManagerProps) {
  const defaultOverrides = useDefaultContractOverrides();

  const useMintPublic = ({
    num,
    numMax,
    nonce,
    signature,
    priceWei,
  }: {
    nonce: number;
    num: number;
    numMax: number;
    priceWei: bigint;
    signature: string;
  }): ContractActionObject => {
    const overrides = {
      ...defaultOverrides,
      value: priceWei,
    };
    return useCreateContractActionObject({
      abi,
      address: contractAddress,
      args: [num, numMax, nonce, signature],
      functionName: 'mintPublic',
      ...overrides,
    });
  };

  const useMintPresale = ({
    num,
    numMax,
    nonce,
    signature,
    priceWei,
    toAddress,
  }: {
    nonce: number;
    num: number;
    numMax: number;
    priceWei: bigint;
    signature: string;
    toAddress: HexString;
  }): ContractActionObject => {
    const overrides = {
      ...defaultOverrides,
      value: priceWei,
    };
    return useCreateContractActionObject({
      abi,
      address: contractAddress,
      args: [toAddress, num, numMax, nonce, signature],
      functionName: 'mintEarlyAccess',
      ...overrides,
    });
  };

  const usePublicMintPrice = () => {
    const result = useReadContract({
      abi,
      address: contractAddress,
      functionName: 'MINT_PRICE',
    });
    const invalidate = useCallback(() => {
      result.refetch();
    }, [result]);

    return [
      (result.data as bigint) || BigNumber.from('0').toBigInt(),
      invalidate,
    ] as const;
  };

  const useTotalMinted = () => {
    const result = useReadContract({
      abi,
      address: contractAddress,
      functionName: 'totalSupply',
    });
    const invalidate = useCallback(() => {
      result.refetch();
    }, [result]);

    return [
      (result.data as bigint) || BigNumber.from('0').toBigInt(),
      invalidate,
    ] as const;
  };

  const useMaxSupply = () => {
    const result = useReadContract({
      abi,
      address: contractAddress,
      functionName: 'MAX_NUM_TOKENS',
    });
    const invalidate = useCallback(() => {
      result.refetch();
    }, [result]);

    return [(result.data as number) || 0, invalidate] as const;
  };

  const useTokenUri = (tokenId: number): string => {
    const result = useReadContract({
      abi,
      address: contractAddress,
      args: [tokenId],
      functionName: 'tokenURI',
    });

    return (result.data as string) || '';
  };

  const useTokenSeed = (tokenId: number): string => {
    const result = useReadContract({
      abi,
      address: contractAddress,
      args: [tokenId],
      functionName: 'tokenSeed',
    });

    return (result.data as string) || '';
  };

  return {
    useMaxSupply,
    useMintPresale,
    useMintPublic,
    usePublicMintPrice,
    useTokenSeed,
    useTokenUri,
    useTotalMinted,
  };
}
