import { useEffect } from 'react';
import { utils } from 'ethers';
import { useWaitForTransactionReceipt } from 'wagmi';

import { LoaderIcon } from '@mp-frontend/core-components/icons';
import { joinClasses } from '@mp-frontend/core-utils';

import StandardDialog from 'components/dialogs/StandardDialog';
import { PendingOnChainDisclaimer } from 'pages/product/ProductPendingOnChain';
import CSSGap from 'types/enums/css/Gap';
import CSSGlobal from 'types/enums/css/Global';
import { HexString } from 'utils/jwt/walletUtils';

interface MintPendingOnChainProps {
  onSuccess: (tokenId: number) => void;
  transactionHash: string;
}

export default function MintPendingOnChain({
  transactionHash,
  onSuccess,
}: MintPendingOnChainProps) {
  const transactionReceipt = useWaitForTransactionReceipt({
    hash: transactionHash as HexString,
    pollingInterval: 1000,
  });

  // Transfer event hash.
  const transferHash = utils.keccak256(
    utils.toUtf8Bytes('Transfer(address,address,uint256)')
  );

  useEffect(() => {
    if (
      transactionReceipt.isLoading ||
      !transactionReceipt.isSuccess ||
      !transactionReceipt.data
    ) {
      return;
    }

    // Find the transfer log.
    const transferLog = transactionReceipt.data.logs.filter(
      (log) => log.topics[0].toLowerCase() === transferHash.toLowerCase()
    )[0];

    // logs should have the tokenId as the 4th topic.
    const tokenOnChainId = parseInt(transferLog.topics[3], 16);
    onSuccess(tokenOnChainId);
  }, [transactionReceipt, transferHash, onSuccess]);

  return transactionHash ? (
    <StandardDialog title="Make a Purchase">
      <div className={joinClasses(CSSGlobal.Flex.CenteredCol, CSSGap[16])}>
        <LoaderIcon fontSize="68" />
        <PendingOnChainDisclaimer transactionId={transactionHash} />
      </div>
    </StandardDialog>
  ) : null;
}
