import { useCallback, useState } from 'react';
import { Link, LinkProps } from 'react-router-dom';

import { MPActionButton } from '@mp-frontend/core-components';

import { NFTContractQuery } from 'graphql/__generated__/NFTContractQuery.graphql';

import LoginRequiredButton from 'components/LoginRequiredButton';
import ROUTES from 'constants/Routes';
import ProductActionsContainer from 'pages/product/productActions/ProductActionsContainer';
import ProductNotLiveActions from 'pages/product/productActions/ProductNotLiveActions';
import CSSMargin from 'types/enums/css/Margin';
import {
  OpenEditionDropMetadataType,
  OpenEditionType,
} from 'types/graphql/OpenEdition';
import { hasDateBetween } from 'utils/hasDatePassed';
import { getNFTPresaleState } from 'utils/nftUtils';

import GenerativePurchase from './purchase';

function LinkComponent(
  props: Omit<LinkProps, 'to'> & {
    href: string;
  }
) {
  return <Link to={props.href} {...props} />;
}

interface GenerativeActionsProps {
  authorFullName: string;
  authorStoreSlug: string;
  dropMetadata: {
    drop: Pick<
      OpenEditionDropMetadataType['drop'],
      | 'artworksAvailable'
      | 'dropTitle'
      | 'dropPhase'
      | 'isEligibleForPresale'
      | 'pk'
      | 'presale'
      | 'subscribePhoneNumber'
    >;
  };
  dropsAt: OpenEditionDropMetadataType['dropsAt'];
  endsAt: OpenEditionDropMetadataType['endsAt'];
  generativeCollectionSlug: string;
  generativeContract: NFTContractQuery['response']['nftContract'];
  invalidate: () => void;
  isSoldOut: boolean;
  openEdition: Pick<OpenEditionType, 'nftMetadata' | 'pk' | 'slug'>;
  priceInEth: number;
  priceInEthWei: bigint;
  priceInUsd: number;
  title: string;
}

export default function GenerativeActions({
  authorFullName,
  authorStoreSlug,
  dropMetadata,
  dropsAt,
  endsAt,
  generativeCollectionSlug,
  generativeContract,
  isSoldOut,
  openEdition,
  priceInEth,
  priceInEthWei,
  priceInUsd,
  title,
  invalidate,
}: GenerativeActionsProps) {
  const [showDialog, setShowDialog] = useState<boolean>(false);
  const handleOpenDialog = useCallback(() => setShowDialog(true), []);
  const handleCloseDialog = useCallback(() => setShowDialog(false), []);
  const { showPresale, isPresaleActive, isPresaleEligible } =
    getNFTPresaleState(!!priceInEth, dropMetadata);
  const canBuy =
    !isSoldOut &&
    dropMetadata?.drop?.artworksAvailable &&
    hasDateBetween(dropsAt, endsAt);

  return (
    <>
      <ProductActionsContainer className={CSSMargin.BOTTOM[32]}>
        {canBuy ? (
          <LoginRequiredButton
            variant="primary"
            fullWidth
            size="large"
            onClick={handleOpenDialog}
          >
            Purchase
          </LoginRequiredButton>
        ) : isSoldOut ? (
          <MPActionButton
            href={ROUTES.PROFILE.SHOP(generativeCollectionSlug)}
            LinkComponent={LinkComponent}
            variant="primary"
            fullWidth
            size="large"
          >
            Buy on Secondary Market
          </MPActionButton>
        ) : (
          <ProductNotLiveActions
            dropMetadata={dropMetadata}
            dropsAt={dropsAt}
            isAuctionFinalizing={false}
            isPresaleActive={isPresaleActive}
            isPresaleEligible={isPresaleEligible}
            showPresale={showPresale}
            invalidate={invalidate}
            onPresalePurchaseClick={handleOpenDialog}
          />
        )}
      </ProductActionsContainer>

      {!!showDialog && (
        <GenerativePurchase
          authorFullName={authorFullName}
          authorStoreSlug={authorStoreSlug}
          generativeContract={generativeContract}
          isPresale={!!(isPresaleActive && isPresaleEligible)}
          invalidate={invalidate}
          openEditionId={parseInt(openEdition.pk, 10)}
          openEditionSlug={openEdition.slug}
          priceInEth={priceInEth}
          priceInEthWei={priceInEthWei}
          priceInUsd={priceInUsd}
          title={title}
          onClose={handleCloseDialog}
        />
      )}
    </>
  );
}
