import { useCallback, useEffect, useRef, useState } from 'react';
import { useMutation } from 'react-relay';
import { useAccount, useSwitchChain } from 'wagmi';

import NFTLogEthTransactionMutation, {
  NFTLogEthTransactionMutation$data,
  NFTLogEthTransactionMutation$variables,
} from 'graphql/__generated__/NFTLogEthTransactionMutation.graphql';
import NFTSignGenerativeMintMessageMutation, {
  NFTSignGenerativeMintMessageMutation$data,
  NFTSignGenerativeMintMessageMutation$variables,
} from 'graphql/__generated__/NFTSignGenerativeMintMessageMutation.graphql';

import { useTrackingContext } from 'components/trackingContext';
import GTM from 'GTM';
import useGmStudioContract from 'hooks/contracts/useGmStudioContract';
import useLogging from 'hooks/useLogging';
import useSession from 'hooks/useSession';
import useWalletValidate from 'hooks/wallet/useWalletValidate';
import CurrencyDisplayMode from 'types/enums/CurrencyDisplayMode';
import { HexString } from 'utils/jwt/walletUtils';
import { promisifyMutationWithRequestData } from 'utils/promisifyMutation';

const NUM_OF_EDITIONS = 1;

interface usePurchaseGenerativeWithEthereumProps {
  contractAddress: HexString;
  isPresale: boolean;
  openEditionId: number;
  openEditionTitle: string;
  priceInEth: number;
  priceInUsd: number;
  priceWei: bigint;
  storeSlug: string;
  useMintPresale: ReturnType<typeof useGmStudioContract>['useMintPresale'];
  useMintPublic: ReturnType<typeof useGmStudioContract>['useMintPublic'];
}

export function usePurchaseGenerativeWithEthereum({
  contractAddress,
  isPresale,
  openEditionId,
  openEditionTitle,
  priceWei,
  priceInEth,
  priceInUsd,
  storeSlug,
  useMintPresale,
  useMintPublic,
}: usePurchaseGenerativeWithEthereumProps) {
  const { source } = useTrackingContext();
  const { logNFTException } = useLogging();
  const { address: currentUserAddress } = useAccount();
  const { contractNetwork } = useSession();
  const { switchChainAsync } = useSwitchChain();
  const [transactionHash, setTransactionHash] = useState<string | null>(null);
  const resetTransactionHash = useCallback(() => setTransactionHash(null), []);
  const [mintMessage, setMintMessage] = useState<{
    nonce: number;
    signature: string;
  }>(null);
  const validationState = useWalletValidate({
    warnIfAddressNotRegistered: true,
  });
  const purchasePromistRef = useRef<{
    reject: (error: Error) => void;
    resolve: (value: string) => void;
  }>(null);

  const signGenerativeMessageMutationAsync = promisifyMutationWithRequestData<
    NFTSignGenerativeMintMessageMutation$variables,
    NFTSignGenerativeMintMessageMutation$data
  >(useMutation(NFTSignGenerativeMintMessageMutation)[0]);

  const logTransactionMutationAsync = promisifyMutationWithRequestData<
    NFTLogEthTransactionMutation$variables,
    NFTLogEthTransactionMutation$data
  >(useMutation(NFTLogEthTransactionMutation)[0]);

  const mintPresale = useMintPresale({
    nonce: mintMessage?.nonce,
    num: NUM_OF_EDITIONS,
    numMax: NUM_OF_EDITIONS,
    priceWei,
    signature: mintMessage?.signature,
    toAddress: currentUserAddress,
  });

  const mintPublic = useMintPublic({
    nonce: mintMessage?.nonce,
    num: NUM_OF_EDITIONS,
    numMax: NUM_OF_EDITIONS,
    priceWei,
    signature: mintMessage?.signature,
  });

  // Effect to handle the purchase once we have got signature and nonce.
  useEffect(() => {
    if (!mintMessage || !currentUserAddress) return;

    (async () => {
      try {
        if (validationState.isWrongAddress) {
          await switchChainAsync({ chainId: contractNetwork });
        }

        const transactionResult = await (isPresale
          ? mintPresale
          : mintPublic
        ).mutate.writeAsync();
        setTransactionHash(transactionResult);
        GTM.ecommerce.purchaseOpenEdition(
          openEditionTitle,
          priceInEth,
          priceInUsd,
          openEditionId,
          storeSlug,
          1,
          source,
          CurrencyDisplayMode.ETH
        );
        await logTransactionMutationAsync({
          fromAddress: currentUserAddress,
          nonce: mintMessage.nonce,
          txid: transactionResult,
        });
        purchasePromistRef.current.resolve(transactionResult);
      } catch (error) {
        GTM.ecommerce.error(error.toString());
        logNFTException(openEditionId, `Generative: ${error.message}`);
        purchasePromistRef.current.reject(error);
      }
      purchasePromistRef.current = null;
    })();
    /* eslint-disable-next-line react-hooks/exhaustive-deps */
  }, [mintMessage, currentUserAddress, openEditionId]);

  const purchase = useCallback(async (): Promise<string> => {
    if (!currentUserAddress) return Promise.resolve(null);

    const promise = new Promise<string>((resolve, reject) => {
      signGenerativeMessageMutationAsync({
        contractAddress,
        numMax: NUM_OF_EDITIONS,
        openEditionId,
        toAddress: currentUserAddress,
      })
        .then((response) => {
          purchasePromistRef.current = { reject, resolve };
          setMintMessage(response.signGenerativeMintMessage);
        })
        .catch((error) => reject(error));
    });

    return promise;
  }, [
    currentUserAddress,
    contractAddress,
    openEditionId,
    signGenerativeMessageMutationAsync,
  ]);

  return [purchase, transactionHash, resetTransactionHash] as const;
}

export default { usePurchaseGenerativeWithEthereum };
