import pluralize from 'pluralize';

import { MPDivider } from '@mp-frontend/core-components';

import FullArtist from 'components/accounts/FullArtist';
import MiniArtist from 'components/accounts/MiniArtist';
import { AccountArtistFragment } from 'types/graphql/Account';

import Section from './Section';

interface ArtistsProps {
  artists: ReadonlyArray<AccountArtistFragment>;
}

export default function Artists({ artists }: ArtistsProps) {
  const Slot = artists.length > 2 ? MiniArtist : FullArtist;

  return artists.length ? (
    <Section title={pluralize('Artist', artists.length)}>
      {artists.map((artist, index) => (
        <>
          <Slot artist={artist} key={artist.id} />

          {Slot === FullArtist && index < artists.length - 1 && <MPDivider />}
        </>
      ))}
    </Section>
  ) : null;
}
