import { Link } from 'react-router-dom';

import { MPActionButton } from '@mp-frontend/core-components';

import ROUTES from 'constants/Routes';
import ProductActionsContainer from 'pages/product/productActions/ProductActionsContainer';
import { DropSectionSubSectionItemsType } from 'types/graphql/Drop';
import { NFTType } from 'types/graphql/NFT';
import { OpenEditionDropMetadataType } from 'types/graphql/OpenEdition';
import { hasDateBetween } from 'utils/hasDatePassed';

interface CollectionCoreActionsProps {
  dropMetadata: {
    drop: Pick<OpenEditionDropMetadataType['drop'], 'artworksAvailable'>;
  };
  dropsAt: OpenEditionDropMetadataType['dropsAt'];
  endsAt: OpenEditionDropMetadataType['endsAt'];
  slug: NFTType['metadata']['nftCollection']['slug'];
  isSoldOut?: DropSectionSubSectionItemsType['isSoldOut'];
}

export default function CollectionCoreActions({
  dropMetadata,
  dropsAt,
  endsAt,
  isSoldOut,
  slug,
}: CollectionCoreActionsProps) {
  const canBuy =
    dropMetadata?.drop?.artworksAvailable &&
    !isSoldOut &&
    hasDateBetween(dropsAt, endsAt);
  return (
    <ProductActionsContainer>
      {canBuy ? (
        <Link to={ROUTES.COLLECTION_DROP(slug)} reloadDocument>
          <MPActionButton variant="primary" fullWidth size="large">
            Purchase
          </MPActionButton>
        </Link>
      ) : null}
    </ProductActionsContainer>
  );
}
