import { Link } from 'react-router-dom';

import { joinClasses } from '@mp-frontend/core-utils';

import ROUTES from 'constants/Routes';
import CSSGlobal from 'types/enums/css/Global';
import getProfileImage from 'utils/getProfileImage';

import * as styles from 'css/components/avatar/StackedAvatars.module.css';

export interface StackedAvatarCreatorType {
  id: string;
  profileImageUrl: string;
  username: string;
}

interface StackedAvatarsProps {
  creators: ReadonlyArray<StackedAvatarCreatorType>;
  onClick?: (creator: StackedAvatarCreatorType) => void;
}

function StackedAvatars({ creators, onClick }: StackedAvatarsProps) {
  return (
    <div className={joinClasses(styles.avatarContainer, CSSGlobal.NoScrollbar)}>
      {creators.map((creator, index) => (
        <Link
          to={ROUTES.PROFILE(creator.username)}
          className={joinClasses(styles.avatar, {
            [styles.largeAvatar]: creators.length <= 5,
            [styles.mediumAvatar]: creators.length <= 10 && creators.length > 5,
            [styles.smallAvatar]: creators.length > 10,
          })}
          key={creator.id}
          style={{
            backgroundImage: `url(${getProfileImage(creator.profileImageUrl)})`,
            zIndex: index + 1,
          }}
          onClick={() => onClick?.(creator)}
        />
      ))}
    </div>
  );
}

export default StackedAvatars;
