import ProductDefaultLabel from 'pages/product/productLabels/ProductDefaultLabel';
import ProductPriceLabel from 'pages/product/productLabels/ProductPriceLabel';
import ProductTimerLabel, {
  TimerType,
} from 'pages/product/productLabels/ProductTimerLabel';
import { OpenEditionDropMetadataType } from 'types/graphql/OpenEdition';
import {
  generateShortEthPriceString,
  generateShortUsdPriceString,
} from 'utils/currency/generatePricing';
import hasDatePassed from 'utils/hasDatePassed';
import { getNFTPresaleState } from 'utils/nftUtils';

interface GenerativeLabelsProps {
  dropMetadata: Omit<
    OpenEditionDropMetadataType,
    'dropsAt' | 'endsAt' | 'listing'
  >;
  dropsAt: OpenEditionDropMetadataType['dropsAt'];
  endsAt: OpenEditionDropMetadataType['endsAt'];
  forceUpdate: () => void;
  isSoldOut: boolean;
  maxSupply: number;
  priceInEth: number;
  priceInUsd: number;
  totalMinted: bigint;
}

export default function GenerativeLabels({
  dropMetadata,
  dropsAt,
  endsAt,
  isSoldOut,
  maxSupply,
  priceInEth,
  priceInUsd,
  totalMinted,
  forceUpdate,
}: GenerativeLabelsProps) {
  const { showPresale, isPresaleActive, isPresaleEligible } =
    getNFTPresaleState(!!priceInEth, dropMetadata);

  return (
    <>
      {(!isPresaleEligible || !isPresaleActive) && (
        <ProductPriceLabel
          label={isPresaleActive ? 'Price' : 'Buy Now'}
          isUSDPrimary={false}
          usdPrice={generateShortUsdPriceString(priceInUsd)}
          ethPrice={generateShortEthPriceString(priceInEth)}
        />
      )}
      {!!showPresale && (
        <ProductPriceLabel
          label="Pre-Sale Price"
          isHidden={!isPresaleEligible}
          isPremium={isPresaleEligible}
          isUSDPrimary={false}
          usdPrice={generateShortUsdPriceString(priceInUsd)}
          ethPrice={generateShortEthPriceString(priceInEth)}
        />
      )}
      {!isSoldOut && !hasDatePassed(endsAt) ? (
        <ProductDefaultLabel
          label="Sold"
          value={`${totalMinted} / ${maxSupply}`}
        />
      ) : (
        <ProductDefaultLabel label="Artworks Sold" value={`${totalMinted}`} />
      )}
      {showPresale && isPresaleEligible ? (
        <ProductTimerLabel
          type={TimerType.Presale}
          startDate={dropMetadata.drop.presale.startsAt}
          endDate={dropMetadata.drop.presale.endsAt}
          onEnd={forceUpdate}
        />
      ) : (
        <>
          {!isSoldOut ? (
            <ProductTimerLabel
              type={TimerType.OpenEdition}
              startDate={dropsAt}
              endDate={endsAt}
              upcomingLabel={
                showPresale && !isPresaleEligible
                  ? 'Public Sale Starts In'
                  : undefined
              }
              onEnd={forceUpdate}
            />
          ) : null}
        </>
      )}
    </>
  );
}
