import { useCallback } from 'react';
import { usePaginationFragment, usePreloadedQuery } from 'react-relay';

import {
  MPActionButton,
  MPBackgroundColorClass,
  MPFonts,
  useIsMobile,
} from '@mp-frontend/core-components';
import { joinClasses } from '@mp-frontend/core-utils';

import ActivityPaginatedFragmentType, {
  ActivityPaginatedFragment$key,
} from 'graphql/__generated__/ActivityPaginatedFragment.graphql';
import ActivityPaginatedQueryType, {
  ActivityPaginatedQuery,
} from 'graphql/__generated__/ActivityPaginatedQuery.graphql';

import StackedAvatars, {
  StackedAvatarCreatorType,
} from 'components/avatar/StackedAvatars';
import ROUTES from 'constants/Routes';
import { DropClickEventType } from 'GTM';
import { ActivityPageTable } from 'pages/activity/ActivityPageTable';
import PageSection from 'pages/drops/components/PageSection';
import CSSGlobal from 'types/enums/css/Global';
import { AccountArtistFragment } from 'types/graphql/Account';
import useDropPageGTM from 'utils/GTM/drop';
import withDefaultErrorBoundary from 'utils/hocs/withDefaultErrorBoundary';
import withLoadQuery, { WithLoadQueryProps } from 'utils/hocs/withLoadQuery';

import * as styles from 'css/pages/drops/components/Activity.module.css';

interface ActivityProps {
  activityQuery: WithLoadQueryProps<ActivityPaginatedQuery>;
  artists: ReadonlyArray<AccountArtistFragment>;
}

function Activity({ activityQuery, artists }: ActivityProps) {
  const isMobile = useIsMobile();
  const track = useDropPageGTM();
  const paginationFragment = usePaginationFragment<
    ActivityPaginatedQuery,
    ActivityPaginatedFragment$key
  >(
    ActivityPaginatedFragmentType,
    usePreloadedQuery<ActivityPaginatedQuery>(
      ActivityPaginatedQueryType,
      activityQuery.queryRef
    )
  );

  const AllActivityCta = (
    <MPActionButton
      size="large"
      variant="secondary"
      href={ROUTES.ACTIVITY.WITH_DROP_CREATORS_FILTER(
        artists.map((creator) => creator.username)
      )}
      onClick={() =>
        track.trackClickEventType(DropClickEventType.DropsActivityViewAll)
      }
    >
      View All
    </MPActionButton>
  );

  const handleClick = useCallback(
    (creator: StackedAvatarCreatorType) => {
      track.trackClickCreatorActivityAvatar(
        DropClickEventType.ActivityCreatorClick,
        creator.username
      );
    },
    [track]
  );

  return (
    !!artists.length &&
    !!paginationFragment.data.results.edges.length && (
      <PageSection className={MPBackgroundColorClass.CommonWhite}>
        <div className={styles.activityTitleContainer}>
          <div className={joinClasses(MPFonts.headline1, styles.title)}>
            {artists.length > 1
              ? `More from these Artists`
              : `More from this Artist`}
          </div>
          <StackedAvatars creators={artists} onClick={handleClick} />
        </div>

        <div className={styles.activityTableContainer}>
          <div className={CSSGlobal.Flex.RowSpaceBetween}>
            <div className={MPFonts.headline3}>Recent Sales</div>
            {!isMobile && AllActivityCta}
          </div>

          <ActivityPageTable
            hasInfiniteScroll={false}
            showActivityHeader={false}
            className={styles.activityTable}
            cardClassName={styles.activityCard}
            activityQuery={activityQuery}
          />
          {!!isMobile && AllActivityCta}
        </div>
      </PageSection>
    )
  );
}

export default withDefaultErrorBoundary(
  withLoadQuery(Activity, {
    activityQuery: { concreteRequest: ActivityPaginatedQueryType },
  }),
  { hideState: true }
);
