import { usePreloadedQuery } from 'react-relay';

import {
  MPAnimations,
  MPColorClass,
  useIsMobile,
} from '@mp-frontend/core-components';
import { joinClasses } from '@mp-frontend/core-utils';

import DropQueryRequest, {
  DropQuery,
} from 'graphql/__generated__/DropQuery.graphql';

import User, { UserProps } from 'components/accounts/User';

import ArtistsMarquee from './ArtistsMarquee';

import * as styles from 'css/pages/drops/components/ArtistsSection.module.css';

const userClasses: Pick<
  UserProps,
  'nameColorClassName' | 'statsColorClassName'
> = {
  nameColorClassName: joinClasses(
    MPColorClass.CommonWhite,
    MPAnimations.Color.LightToDark
  ),
  statsColorClassName: joinClasses(
    MPColorClass.SolidNeutralGray1,
    styles.transparentStats
  ),
};

export default function ArtistsSection({ queryRef }) {
  const { creators: artists } = usePreloadedQuery<DropQuery>(
    DropQueryRequest,
    queryRef
  ).drops.edges[0].node;

  const isMobile = useIsMobile();
  return artists.length === 1 ? (
    <div className={joinClasses(styles.artists, styles.single)}>
      <User
        user={artists[0]}
        size="large"
        className={styles.artist}
        {...userClasses}
      />
    </div>
  ) : isMobile ? (
    <ArtistsMarquee
      artists={artists}
      artistProps={{
        className: styles.artist,
        ...userClasses,
      }}
    />
  ) : (
    <div
      className={joinClasses(
        styles.artists,
        artists.length > 5 && styles.multiRow
      )}
    >
      {artists.map((artist) => (
        <User
          key={artist.id}
          user={artist}
          className={styles.artist}
          {...userClasses}
        />
      ))}
    </div>
  );
}
