import { ReactNode } from 'react';

import {
  MPBackgroundColorClass,
  MPColorClass,
  MPFonts,
} from '@mp-frontend/core-components';
import { joinClasses } from '@mp-frontend/core-utils';

import CSSGlobal from 'types/enums/css/Global';

import * as styles from 'css/pages/product/ProductNotice.module.css';

interface ProductNoticeProps {
  children: ReactNode;
  className?: string;
}

export default function ProductNotice({
  children,
  className,
}: ProductNoticeProps) {
  return (
    <div
      className={joinClasses(
        CSSGlobal.Cursor.Default,
        MPBackgroundColorClass.BackgroundDefault,
        MPColorClass.CommonBlack,
        MPFonts.paragraphSmall,
        styles.content,
        className
      )}
    >
      {children}
    </div>
  );
}
