import { startTransition, useEffect, useState } from 'react';
import { usePreloadedQuery } from 'react-relay';
import { formatEther } from 'viem';

import NFTContractQueryType, {
  NFTContractQuery,
} from 'graphql/__generated__/NFTContractQuery.graphql';

import { TrackingContext } from 'components/trackingContext';
import ROUTES from 'constants/Routes';
import useGmStudioContract from 'hooks/contracts/useGmStudioContract';
import ProductPageCardLayout from 'pages/product/card/Layout';
import ProductCardTopSection from 'pages/product/card/ProductCardTopSection';
import { ProductDescriptions } from 'pages/product/ProductDescription';
import ProductNotice from 'pages/product/ProductNotice';
import CSSMargin from 'types/enums/css/Margin';
import withDefaultErrorBoundary from 'utils/hocs/withDefaultErrorBoundary';
import withLoadQuery, { WithLoadQueryProps } from 'utils/hocs/withLoadQuery';
import { HexString } from 'utils/jwt/walletUtils';
import { getFirstMintedProductSlug } from 'utils/openEditionUtils';

import OpenEditionLeaderboard, {
  OpenEditionUpdateEvent,
} from '../openEdition/Leaderboard';
import { OpenEditionPageCardMetadataType } from '../openEdition/OpenEditionPageCard';
import GenerativeActions from './GenerativeActions';
import GenerativeLabels from './GenerativeLabels';
import useSeedLiveRenderUrl from './useSeedLiveRenderUrl';

interface GenerativePageCardProps {
  contractQuery: WithLoadQueryProps<NFTContractQuery>;
  nftMetadata: OpenEditionPageCardMetadataType;
}

function GenerativePageCard({
  contractQuery,
  nftMetadata,
}: GenerativePageCardProps) {
  const { nftContract: generativeContract } =
    usePreloadedQuery<NFTContractQuery>(
      NFTContractQueryType,
      contractQuery.queryRef
    );
  const { usePublicMintPrice, useMaxSupply, useTotalMinted } =
    useGmStudioContract({
      abi: JSON.parse(generativeContract.abidata).abi,
      contractAddress: generativeContract.address as HexString,
    });
  const [priceInEthWei] = usePublicMintPrice();
  const [maxSupply] = useMaxSupply();
  const [totalMinted, invalidateTotalMinted] = useTotalMinted();
  const priceInEth = parseFloat(formatEther(priceInEthWei));
  const priceInUsd = priceInEth * nftMetadata.currentEthRate;
  const dropMetadata = nftMetadata?.dropMetadata;
  const openEdition = nftMetadata?.openEdition;
  const firstEditionProductSlug = getFirstMintedProductSlug(openEdition);
  const isSoldOut = totalMinted === BigInt(maxSupply);

  const generativeRendererUrl = useSeedLiveRenderUrl(
    openEdition.slug,
    'random'
  );

  const [{ dropsAt, endsAt }, setOpenEditionState] = useState<
    Pick<OpenEditionUpdateEvent, 'dropsAt' | 'endsAt'>
  >({
    dropsAt: dropMetadata?.dropsAt,
    endsAt: dropMetadata?.endsAt,
  });

  useEffect(() => {
    const interval = setInterval(
      () => startTransition(invalidateTotalMinted),
      5000
    );
    return () => clearInterval(interval);
  }, [invalidateTotalMinted]);

  return (
    <TrackingContext
      title={nftMetadata.title}
      storeSlug={nftMetadata.author.store.storeSlug}
    >
      <ProductPageCardLayout
        nftMetadata={{ ...nftMetadata, isSoldOut }}
        topSection={
          <ProductCardTopSection
            link={
              isSoldOut && firstEditionProductSlug
                ? ROUTES.NFT(firstEditionProductSlug)
                : null
            }
            nftMetadata={nftMetadata}
          />
        }
        generativeRendererUrl={generativeRendererUrl}
      >
        <ProductDescriptions
          artwork={{
            description: nftMetadata.description,
            videoPreviewImageUrl: nftMetadata.descriptionImageUrl,
            videoUrl: nftMetadata.descriptionVideoUrl,
          }}
          collectorReward={{
            description: nftMetadata.collectorReward?.description,
          }}
        />
        <GenerativeLabels
          dropMetadata={dropMetadata}
          dropsAt={dropsAt}
          endsAt={endsAt}
          isSoldOut={isSoldOut}
          maxSupply={maxSupply}
          priceInEth={priceInEth}
          priceInUsd={priceInUsd}
          totalMinted={totalMinted}
          forceUpdate={invalidateTotalMinted}
        />
        <ProductNotice className={CSSMargin.BOTTOM[24]}>
          Each buyer will get a unique artwork from the artist&apos;s algorithm,
          different from what&apos;s being displayed.
        </ProductNotice>
        <GenerativeActions
          authorFullName={nftMetadata.author.fullName}
          authorStoreSlug={nftMetadata.author.store.storeSlug}
          dropMetadata={dropMetadata}
          dropsAt={dropsAt}
          endsAt={endsAt}
          generativeCollectionSlug={dropMetadata.drop.generativeSlug}
          generativeContract={generativeContract}
          isSoldOut={isSoldOut}
          openEdition={openEdition}
          priceInEth={priceInEth}
          priceInEthWei={priceInEthWei}
          priceInUsd={priceInUsd}
          title={nftMetadata.title}
          invalidate={invalidateTotalMinted}
        />
        <OpenEditionLeaderboard
          collapsible
          hasBottomBorder
          hideIfEmpty
          openEditionSlug={openEdition.slug}
          onOpenEditionUpdate={setOpenEditionState}
        />
      </ProductPageCardLayout>
    </TrackingContext>
  );
}

export default withDefaultErrorBoundary(
  withLoadQuery(GenerativePageCard, {
    contractQuery: { concreteRequest: NFTContractQueryType },
  }),
  {
    hideState: true,
  }
);
