import { useCallback, useState } from 'react';

import { NFTContractQuery } from 'graphql/__generated__/NFTContractQuery.graphql';

import useGmStudioContract from 'hooks/contracts/useGmStudioContract';
import { usePurchaseGenerativeWithEthereum } from 'hooks/product/usePurchaseGenerative';
import withDefaultErrorBoundary from 'utils/hocs/withDefaultErrorBoundary';
import { HexString } from 'utils/jwt/walletUtils';

import useSeedLiveRenderUrl from '../useSeedLiveRenderUrl';
import CongratulationsDialog from './CongratulationsDialog';
import MintPendingOnChain from './MintPendingOnChain';
import PurchaseDialog from './PurchaseDialog';

export enum PurchaseFlowStep {
  Purchase = 'Purchase',
  Success = 'Success',
}

interface GenerativePurchaseProps {
  authorFullName: string;
  authorStoreSlug: string;
  generativeContract: NFTContractQuery['response']['nftContract'];
  isPresale: boolean;
  onClose: () => void;
  openEditionId: number;
  openEditionSlug: string;
  priceInEth: number;
  priceInEthWei: bigint;
  priceInUsd: number;
  title: string;
  invalidate?: () => void;
}

function GenerativePurchase({
  authorFullName,
  authorStoreSlug,
  generativeContract,
  isPresale,
  openEditionId,
  openEditionSlug,
  priceInEth,
  priceInEthWei,
  priceInUsd,
  title,
  invalidate,
  onClose,
}: GenerativePurchaseProps) {
  const [step, setStep] = useState<PurchaseFlowStep>(PurchaseFlowStep.Purchase);
  const [tokenId, setTokenId] = useState<number>(null);

  const { useMintPresale, useMintPublic, useTokenSeed } = useGmStudioContract({
    abi: JSON.parse(generativeContract.abidata).abi,
    contractAddress: generativeContract.address as HexString,
  });

  const [purchaseWithEthereum, transactionHash, resetTransactionHash] =
    usePurchaseGenerativeWithEthereum({
      contractAddress: generativeContract.address as HexString,
      isPresale,
      openEditionId,
      openEditionTitle: title,
      priceInEth,
      priceInUsd,
      priceWei: priceInEthWei,
      storeSlug: authorStoreSlug,
      useMintPresale,
      useMintPublic,
    });
  const tokenSeed = useTokenSeed(tokenId);
  const tokenUri = useSeedLiveRenderUrl(openEditionSlug, tokenSeed);

  const handleSuccess = useCallback(
    (tokenOnChainId: number) => {
      setTokenId(tokenOnChainId);
      setStep(PurchaseFlowStep.Success);
      resetTransactionHash();
      invalidate?.();
    },
    [resetTransactionHash, invalidate]
  );

  const handleFinish = useCallback(() => {
    onClose();
    setTokenId(null);
    setStep(PurchaseFlowStep.Purchase);
    invalidate?.();
  }, [onClose, setStep, invalidate]);

  return {
    [PurchaseFlowStep.Purchase]: transactionHash ? (
      <MintPendingOnChain
        transactionHash={transactionHash}
        onSuccess={handleSuccess}
      />
    ) : (
      <PurchaseDialog
        purchaseWithEthereum={purchaseWithEthereum}
        priceInEth={priceInEth}
        priceInUsd={priceInUsd}
        onClose={onClose}
      />
    ),
    [PurchaseFlowStep.Success]: (
      <CongratulationsDialog
        authorFullName={authorFullName}
        title={title}
        tokenUri={tokenUri}
        onClose={handleFinish}
      />
    ),
  }[step];
}

export default withDefaultErrorBoundary(GenerativePurchase, {
  hideState: true,
});
