import { MouseEvent, useCallback, useMemo } from 'react';
import pluralize from 'pluralize';

import {
  MPAnimations,
  MPColorClass,
  MPFonts,
} from '@mp-frontend/core-components';
import { joinClasses, useOnEnterKey } from '@mp-frontend/core-utils';

import CSSGlobal from 'types/enums/css/Global';
import { DropSectionItemsType } from 'types/graphql/Drop';

import { DROP_SECTION_TO_TITLE_MAPPING } from './DropSection';

import * as styles from 'css/pages/drops/components/sections/DropOverview.module.css';

interface DropOverviewProps {
  sections: DropSectionItemsType[];
}

export default function DropOverview(props: DropOverviewProps) {
  const sections = useMemo(
    () =>
      props.sections.reduce<
        Array<{
          itemsCount: number;
          sectionType: string;
        }>
      >((memo, section) => {
        const itemsCount = section.subSections.reduce(
          (count, subSection) => count + subSection.items.length,
          0
        );
        if (itemsCount > 0) {
          memo.push({ itemsCount, sectionType: section.sectionType });
        }

        return memo;
      }, []),
    [props.sections]
  );

  const handleClick = useCallback((event: MouseEvent<HTMLLIElement>) => {
    event.preventDefault();

    const {
      dataset: { sectionType },
    } = event.currentTarget;

    const section = document.querySelector(`[data-section=${sectionType}]`);
    section?.scrollIntoView({ behavior: 'smooth' });
  }, []);
  const handleKeypress = useOnEnterKey(handleClick);

  return (
    <div className={joinClasses(CSSGlobal.NoScrollbar, styles.container)}>
      <ul role="menubar">
        {sections.map((section) => (
          <li
            key={section.sectionType}
            role="menuitem"
            className={joinClasses(
              MPFonts.textSmallMedium,
              MPColorClass.SolidNeutralGray5,
              CSSGlobal.Cursor.Pointer,
              MPAnimations.Color.DarkToLight
            )}
            data-section-type={section.sectionType}
            onClick={handleClick}
            onKeyPress={handleKeypress}
          >
            <span>
              {pluralize(
                DROP_SECTION_TO_TITLE_MAPPING[section.sectionType],
                section.itemsCount
              )}
            </span>
            <span
              className={joinClasses(styles.count, {
                [styles.singleDigit]: section.itemsCount < 10,
              })}
            >
              {section.itemsCount}
            </span>
          </li>
        ))}
      </ul>
    </div>
  );
}
