import { useEffect, useState } from 'react';

import { joinClasses, useRefState } from '@mp-frontend/core-utils';

import User, { UserProps } from 'components/accounts/User';
import useOnBodyResize from 'hooks/useOnBodyResize';
import { AccountArtistFragment } from 'types/graphql/Account';

import * as styles from 'css/pages/drops/components/ArtistsSection.module.css';

interface ArtistsMarqueeProps {
  artistProps: Omit<UserProps, 'user'>;
  artists: ReadonlyArray<AccountArtistFragment>;
}

export default function ArtistsMarquee({
  artistProps,
  artists,
}: ArtistsMarqueeProps) {
  const [marqueeNode, setMarqueeRef] = useRefState<HTMLDivElement>(null);
  const [needsInitialCheck, setNeedsInitialCheck] = useState(true);
  const [shouldMarquee, setShouldMarquee] = useState(false);

  const marqueeStyle = {
    animationDuration: `${(shouldMarquee ? artists.length : 0) * 4}s`,
  };
  const { width: resizeBodyWidth } = useOnBodyResize();

  useEffect(() => {
    setNeedsInitialCheck(true);
  }, [resizeBodyWidth]);

  useEffect(() => {
    if (needsInitialCheck && marqueeNode) {
      setShouldMarquee(marqueeNode.scrollWidth > marqueeNode.offsetWidth);
      setNeedsInitialCheck(false);
    }
  }, [needsInitialCheck, marqueeNode]);

  const marquee = !needsInitialCheck && shouldMarquee;

  return (
    <div
      className={joinClasses(
        styles.marqueeContainer,
        marquee && styles.isAnimating
      )}
      ref={setMarqueeRef}
    >
      <div className={styles.marquee} style={marqueeStyle}>
        {artists.map((artist) => (
          <User key={artist.id} user={artist} {...artistProps} />
        ))}
        {!!marquee &&
          artists.map((artist) => (
            <User key={artist.id} user={artist} {...artistProps} />
          ))}
      </div>
    </div>
  );
}
