import { useState } from 'react';

import { TrackingContext } from 'components/trackingContext';
import ROUTES from 'constants/Routes';
import ProductPageCardLayout from 'pages/product/card/Layout';
import ProductCardTopSection from 'pages/product/card/ProductCardTopSection';
import { ProductDescriptions } from 'pages/product/ProductDescription';
import { DropSectionSubSectionItemsType } from 'types/graphql/Drop';
import { NFTDropMetadataType, NFTType } from 'types/graphql/NFT';
import {
  OpenEditionDropMetadataType,
  OpenEditionType,
} from 'types/graphql/OpenEdition';
import hasDatePassed from 'utils/hasDatePassed';
import { getFirstMintedProductSlug } from 'utils/openEditionUtils';

import OpenEditionLeaderboard, { OpenEditionUpdateEvent } from './Leaderboard';
import OpenEditionActions from './OpenEditionActions';
import OpenEditionLabels from './OpenEditionLabels';

export type OpenEditionPageCardMetadataType = Pick<
  NFTType['metadata'],
  | 'author'
  | 'collaborators'
  | 'description'
  | 'descriptionImageUrl'
  | 'descriptionVideoUrl'
  | 'hasVideo'
  | 'highResImage'
  | 'id'
  | 'nftAdditionalMediasRanked'
  | 'pk'
  | 'rawfileExtension'
  | 'standardImage'
  | 'title'
  | 'thumbnailImage'
  | 'videoUrl'
> & {
  collectorReward: Pick<NFTType['metadata']['collectorReward'], 'description'>;
  contract: Pick<NFTType['contract'], 'address'>;
  currentEthRate: NFTType['currentEthRate'];
  dropMetadata: Pick<NFTDropMetadataType, 'dropsAt' | 'endsAt'> & {
    drop: Pick<
      OpenEditionDropMetadataType['drop'],
      | 'artworksAvailable'
      | 'dropTitle'
      | 'dropPhase'
      | 'isEligibleForPresale'
      | 'pk'
      | 'presale'
      | 'subscribePhoneNumber'
    > & {
      generativeSlug?: string;
    };
  };
  nftCollection: Pick<
    NFTType['metadata']['nftCollection'],
    'collectionName' | 'slug'
  >;
  isSoldOut?: DropSectionSubSectionItemsType['isSoldOut'];
  openEdition?: Pick<
    OpenEditionType,
    'pk' | 'editionsSold' | 'priceInEther' | 'priceInUsd' | 'slug'
  > & {
    isGenerative?: boolean;
    nftMetadata?: DropSectionSubSectionItemsType['openEdition']['nftMetadata'];
  };
};

interface OpenEditionPageCardProps {
  invalidate: () => void;
  nftMetadata: OpenEditionPageCardMetadataType;
}

function OpenEditionPageCard({
  nftMetadata,
  invalidate,
}: OpenEditionPageCardProps) {
  const dropMetadata = nftMetadata?.dropMetadata;
  const openEdition = nftMetadata?.openEdition;
  const firstEditionProductSlug = getFirstMintedProductSlug(openEdition);
  const isSoldOut = hasDatePassed(dropMetadata.endsAt);

  const [{ dropsAt, endsAt, editionsSold }, setOpenEditionState] =
    useState<OpenEditionUpdateEvent>({
      dropsAt: dropMetadata?.dropsAt,
      editionsSold: openEdition?.editionsSold,
      endsAt: dropMetadata?.endsAt,
    });

  return (
    <TrackingContext
      title={nftMetadata.title}
      storeSlug={nftMetadata.author.store.storeSlug}
    >
      <ProductPageCardLayout
        nftMetadata={nftMetadata}
        topSection={
          <ProductCardTopSection
            link={
              isSoldOut && firstEditionProductSlug
                ? ROUTES.NFT(firstEditionProductSlug)
                : ROUTES.OPEN_EDITION(openEdition.slug, openEdition.pk)
            }
            nftMetadata={nftMetadata}
          />
        }
      >
        <ProductDescriptions
          artwork={{
            description: nftMetadata.description,
            videoPreviewImageUrl: nftMetadata.descriptionImageUrl,
            videoUrl: nftMetadata.descriptionVideoUrl,
          }}
          collectorReward={{
            description: nftMetadata.collectorReward?.description,
          }}
        />
        <OpenEditionLeaderboard
          collapsible
          hasBottomBorder
          openEditionSlug={openEdition.slug}
          onOpenEditionUpdate={setOpenEditionState}
        />
        <OpenEditionLabels
          dropMetadata={dropMetadata}
          dropsAt={dropsAt}
          endsAt={endsAt}
          editionsSold={editionsSold}
          openEdition={openEdition}
          forceUpdate={invalidate}
        />
        <OpenEditionActions
          allowFiat={nftMetadata.author.store.acceptsCreditCard}
          dropMetadata={dropMetadata}
          dropsAt={dropsAt}
          endsAt={endsAt}
          openEdition={openEdition}
          invalidate={invalidate}
        />
      </ProductPageCardLayout>
    </TrackingContext>
  );
}

export default OpenEditionPageCard;
