import { useMemo } from 'react';
import { usePreloadedQuery } from 'react-relay';

import DropSectionsQueryType, {
  DropSectionsQuery,
} from 'graphql/__generated__/DropSectionsQuery.graphql';

import PageSection from 'pages/drops/components/PageSection';
import withDefaultErrorBoundary from 'utils/hocs/withDefaultErrorBoundary';
import withLoadQuery, { WithLoadQueryProps } from 'utils/hocs/withLoadQuery';

import DropOverview from './DropOverview';
import DropSection from './DropSection';

import * as styles from 'css/pages/drops/DropPage.module.css';

interface DropSectionsProps {
  dropSectionsQuery: WithLoadQueryProps<DropSectionsQuery>;
}

function DropSections({
  dropSectionsQuery: { queryRef, invalidate },
}: DropSectionsProps) {
  const { drops } = usePreloadedQuery<DropSectionsQuery>(
    DropSectionsQueryType,
    queryRef
  );

  const sections = useMemo(
    () =>
      drops.edges[0].node.sections.reduce((memo, section) => {
        const subSections = section.subSections.filter(
          (subSection) => subSection.items.length
        );

        return subSections.length
          ? [...memo, { ...section, subSections }]
          : memo;
      }, []),
    [drops.edges]
  );

  return (
    !!sections.length && (
      <>
        <DropOverview sections={sections} />

        <PageSection>
          {sections.map((section) => (
            <DropSection
              key={section.sectionType}
              subSectionsItems={section.subSections}
              sectionType={section.sectionType}
              invalidate={invalidate}
            />
          ))}
        </PageSection>
      </>
    )
  );
}

export default withDefaultErrorBoundary(
  withLoadQuery(DropSections, {
    dropSectionsQuery: { concreteRequest: DropSectionsQueryType },
  }),
  { defaultSuspenseProps: { className: styles.loader } }
);
