import { useEffect } from 'react';
import { usePreloadedQuery } from 'react-relay';
import { useParams } from 'react-router-dom';

import { joinClasses } from '@mp-frontend/core-utils';

import DropQueryRequest, {
  DropQuery,
} from 'graphql/__generated__/DropQuery.graphql';
import { ActivityActionEnum } from 'types/__generated__/graphql';

import DefaultSuspense from 'components/DefaultSuspense';
import Footer from 'components/Footer';
import { TrackingContext } from 'components/trackingContext';
import { APP_NAME } from 'constants/Utils';
import FourOFourError from 'errors/FourOFourError';
import { EcommerceSourceType } from 'GTM';
import CSSMargin from 'types/enums/css/Margin';
import CurrencyDisplayMode from 'types/enums/CurrencyDisplayMode';
import withLoadQuery, { WithLoadQueryProps } from 'utils/hocs/withLoadQuery';
import setDocTitle from 'utils/setDocTitle';

import Activity from './components/Activity';
import GenerativeArtworks from './components/GenerativeArtworks';
import HeroSection from './components/HeroSection';
import DropSections from './components/sections';
import DropsStatusBar from './components/StatusBar';

import * as styles from 'css/pages/drops/DropPage.module.css';

const MAX_ACTIVITY_COUNT = 5;

interface DropPageParams {
  dropQuery: WithLoadQueryProps<DropQuery>;
}

const DropPage = withLoadQuery(
  ({ dropQuery: { queryRef, invalidate } }: DropPageParams) => {
    const { drops } = usePreloadedQuery<DropQuery>(DropQueryRequest, queryRef);
    const drop = drops.edges[0]?.node;

    useEffect(() => {
      if (!drop?.dropTitle) return;

      setDocTitle(`${drop.dropTitle} | ${APP_NAME}`);
    }, [drop?.dropTitle]);

    if (!drop) throw new FourOFourError();
    const { hasGenerative, generativeSlug } = drop;
    const showGenerativeArt = hasGenerative && generativeSlug;

    return (
      <TrackingContext
        source={EcommerceSourceType.DropPage}
        dropTitle={drop.dropTitle}
      >
        <div className={joinClasses(CSSMargin.BOTTOM[32], styles.dropPage)}>
          <DropsStatusBar invalidate={invalidate} />
          <HeroSection queryRef={queryRef} />
          <DefaultSuspense className={styles.loader}>
            <DropSections
              dropSectionsQuery={{ variables: { dropFamilySlug: drop.slug } }}
            />
            {showGenerativeArt ? (
              <GenerativeArtworks
                dropCollectionNFTsQuery={{
                  variables: { collectionSlug: drop.generativeSlug, first: 12 },
                }}
                collectionSlug={drop.generativeSlug}
              />
            ) : drop.creators?.length ? (
              <Activity
                artists={drop.creators}
                activityQuery={{
                  variables: {
                    actions: [ActivityActionEnum.Sale],
                    currencyMode: CurrencyDisplayMode.USD,
                    first: MAX_ACTIVITY_COUNT,
                    userNames: drop.creators.map((creator) => creator.username),
                  },
                }}
              />
            ) : null}
          </DefaultSuspense>
        </div>

        <Footer />
      </TrackingContext>
    );
  },
  {
    dropQuery: { concreteRequest: DropQueryRequest },
  }
);

export default function DropPageWrapper() {
  const { dropSlug } = useParams();

  return <DropPage dropQuery={{ variables: { dropFamilySlug: dropSlug } }} />;
}
