import { useState } from 'react';
import { usePreloadedQuery } from 'react-relay';

import { MPActionButton, MPFonts } from '@mp-frontend/core-components';
import { joinClasses } from '@mp-frontend/core-utils';

import DropQueryRequest, {
  DropQuery,
} from 'graphql/__generated__/DropQuery.graphql';

import { APP_NAME } from 'constants/Utils';
import { DropClickEventType } from 'GTM';
import useDropPageGTM from 'utils/GTM/drop';

import ArtistsSection from './ArtistsSection';
import MoreInfoDialog from './moreInfoDialog';
import { DropVideoPlayerMode } from './VideoPlayer';
import DropVideoSection from './VideoSection';

import * as styles from 'css/pages/drops/components/HeroSection.module.css';

const showRenderPresents = (presenterText: string) =>
  !presenterText || !/present(s|ed)?$/i.test(presenterText);

export default function HeroSection({ queryRef }) {
  const drop = usePreloadedQuery<DropQuery>(DropQueryRequest, queryRef).drops
    .edges[0].node;

  const moreInfoState = useState(false);
  const linA = `rgb(${drop.heroBackgroundColor.r}, ${drop.heroBackgroundColor.g}, ${drop.heroBackgroundColor.b})`;
  const linB = `rgb(${drop.heroBackgroundColor.r}, ${drop.heroBackgroundColor.g}, ${drop.heroBackgroundColor.b}, 0.1)`;
  const track = useDropPageGTM();

  return (
    <div
      className={styles.heroSection}
      style={
        {
          '--dlp-heroBackgroundImage': `url(${drop.heroImageUrl})`,
          '--dlp-heroGradient': `linear-gradient(0deg, ${linA} var(--dlp-heroGradientHeight), ${linB} 100%)`,
          '--dlp-heroGradientHeight': drop.heroVideoUrl ? '60%' : '30%',
        } as any
      }
    >
      <div className={styles.gradient}>
        <div className={styles.centerContent}>
          <p className={joinClasses('reset', MPFonts.textLargeMedium)}>
            {`${drop.presenterText || APP_NAME} ${
              showRenderPresents(drop.presenterText) ? 'Presents' : ''
            }`}
          </p>
          <p className={joinClasses(styles.title, MPFonts.headline1)}>
            {drop.dropTitle}
          </p>
          <ArtistsSection queryRef={queryRef} />
          <DropVideoSection
            source={drop.heroVideoUrl}
            title={drop.dropTitle}
            autoPlay
            loop
            cover
            mode={DropVideoPlayerMode.HERO}
          />
          <p
            className={joinClasses(styles.description, MPFonts.paragraphLarge)}
          >
            {drop.heroDescription}
          </p>
          {!!drop.moreAboutDescription && (
            <>
              <MPActionButton
                variant="tertiary-custom"
                className={joinClasses(styles.moreInfoBtn)}
                onClick={() => {
                  moreInfoState[1](true);
                  track.trackClickEventType(
                    DropClickEventType.MoreAboutThisDrop
                  );
                }}
              >
                <span
                  className={joinClasses(
                    MPFonts.textSmallSemiBold,
                    styles.moreInfoBtnTxt
                  )}
                >
                  More about this Exhibition
                </span>
              </MPActionButton>
              <MoreInfoDialog queryRef={queryRef} state={moreInfoState} />
            </>
          )}
        </div>
      </div>
    </div>
  );
}
