import { useCallback, useState } from 'react';

import {
  MPColorClass,
  MPDialogProps,
  MPFonts,
} from '@mp-frontend/core-components';
import { joinClasses } from '@mp-frontend/core-utils';

import { ProductTypeBidEnum } from 'types/__generated__/graphql';

import ROUTES from 'constants/Routes';
import { APP_NAME } from 'constants/Utils';
import useWalletValidate from 'hooks/wallet/useWalletValidate';
import ProductNotice from 'pages/product/ProductNotice';
import PaymentViewBody from 'pages/product/purchaseOfferDialog/paymentView/PaymentViewBody';
import PaymentViewDialog from 'pages/product/purchaseOfferDialog/paymentView/PaymentViewDialog';
import PaymentViewFooter from 'pages/product/purchaseOfferDialog/paymentView/PaymentViewFooter';
import PaymentViewForm from 'pages/product/purchaseOfferDialog/paymentView/PaymentViewForm';
import PaymentViewPricing from 'pages/product/purchaseOfferDialog/paymentView/PaymentViewPricing';
import CSSGlobal from 'types/enums/css/Global';
import CSSMargin from 'types/enums/css/Margin';
import emptyFunc from 'utils/emptyFunc';
import withDefaultErrorBoundary from 'utils/hocs/withDefaultErrorBoundary';

interface PurchaseDialogProps {
  onClose: MPDialogProps['onClose'];
  priceInEth: number;
  priceInUsd: number;
  purchaseWithEthereum: () => Promise<string>;
}

function PurchaseDialog({
  purchaseWithEthereum,
  priceInEth,
  priceInUsd,
  onClose,
}: PurchaseDialogProps) {
  const validationState = useWalletValidate({
    warnIfAddressNotRegistered: true,
  });
  const [isProcessing, setIsProcessing] = useState<boolean>(false);

  const handleClick = useCallback(async () => {
    setIsProcessing(true);
    try {
      await purchaseWithEthereum();
    } finally {
      setIsProcessing(false);
    }
  }, [purchaseWithEthereum]);

  return (
    <PaymentViewDialog title="Make a Purchase" onClose={onClose}>
      <PaymentViewBody>
        <PaymentViewForm
          paymentMethod={ProductTypeBidEnum.Ether}
          stripeCardsQueryRef={null}
          onPaymentMethodChange={emptyFunc}
          onFormChange={emptyFunc}
          acceptFiat={false}
        />
        <PaymentViewPricing
          amountInEth={priceInEth}
          amountInUsd={priceInUsd}
          paymentMethod={ProductTypeBidEnum.Ether}
        />
        <ProductNotice className={CSSMargin.TOP[16]}>
          Each buyer will get a unique artwork from the artist&apos;s algorithm,
          different from what&apos;s being displayed.
        </ProductNotice>
      </PaymentViewBody>
      <PaymentViewFooter
        submitTitle="Purchase"
        onSubmit={handleClick}
        isDisabled={
          !validationState.isValid ||
          validationState.isDisconnected ||
          isProcessing
        }
      >
        {!validationState.isValid ? (
          <div
            className={joinClasses(
              MPFonts.textSmallMedium,
              MPColorClass.ErrorMain,
              CSSGlobal.Cursor.Default,
              CSSGlobal.TextAlign.Centered
            )}
          >
            {validationState.isUnregisteredAddress ? (
              <span>
                You are connected to a wallet which is not attached to
                your&nbsp;{APP_NAME} account. Please login to a different
                wallet, or connect this one in your&nbsp;
                <a
                  href={ROUTES.ACCOUNT_SETTINGS()}
                  className={joinClasses('underline', MPColorClass.ErrorMain)}
                >
                  account settings
                </a>
                .
              </span>
            ) : validationState.isWrongAddress ? (
              'Wrong Address'
            ) : null}
          </div>
        ) : null}
      </PaymentViewFooter>
    </PaymentViewDialog>
  );
}

export default withDefaultErrorBoundary(PurchaseDialog, {
  hideState: true,
});
