import {
  MPActionButton,
  MPColorClass,
  MPDialogProps,
  MPFonts,
} from '@mp-frontend/core-components';
import { TwitterIcon } from '@mp-frontend/core-components/icons';
import { joinClasses } from '@mp-frontend/core-utils';

import { TwitterButton } from 'components/buttons/shareButtons/TwitterButton';
import StandardDialog from 'components/dialogs/StandardDialog';
import useSession from 'hooks/useSession';
import CSSGap from 'types/enums/css/Gap';
import CSSGlobal from 'types/enums/css/Global';
import CSSMargin from 'types/enums/css/Margin';
import generateFormattedUserFullName from 'utils/generateFormattedUserFullName';

import * as styles from 'css/pages/generative/CongratulationsDialog.module.css';

interface CongratulationsDialogProps {
  authorFullName: string;
  onClose: MPDialogProps['onClose'];
  title: string;
  tokenUri: string;
}

export default function CongratulationsDialog({
  authorFullName,
  title,
  tokenUri,
  onClose,
}: CongratulationsDialogProps) {
  const session = useSession();

  return (
    <StandardDialog
      title="Artwork Generated"
      onClose={onClose}
      actionButton={
        <TwitterButton link={window.location.href}>
          <MPActionButton size="medium" variant="primary" fullWidth>
            <TwitterIcon className={CSSMargin.RIGHT[4]} fontSize="16" /> Share
            on Twitter
          </MPActionButton>
        </TwitterButton>
      }
    >
      <div className={joinClasses(CSSGlobal.Flex.CenteredCol, CSSGap[24])}>
        <div className={joinClasses(MPFonts.textSmallRegular, styles.message)}>
          Congratulations! Your artwork is being minted and should be in your
          collection in a few minutes.
        </div>

        <iframe
          allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
          aria-hidden="true"
          className={styles.artwork}
          sandbox="allow-scripts"
          src={tokenUri}
          title="NFT Preview"
        />

        <div className={joinClasses(CSSGlobal.Flex.Col, CSSGap[18])}>
          <div className={joinClasses(CSSGlobal.Flex.CenteredCol, CSSGap[4])}>
            <span className={MPFonts.textNormalMedium}>
              {generateFormattedUserFullName(authorFullName)}
            </span>
            <span
              className={joinClasses(
                MPFonts.textSmallMedium,
                MPColorClass.SolidNeutralGray5
              )}
            >
              {title}
            </span>
          </div>

          <div className={joinClasses(CSSGlobal.Flex.CenteredCol, CSSGap[4])}>
            <span
              className={joinClasses(
                MPFonts.textSmallMedium,
                MPColorClass.SolidNeutralGray5
              )}
            >
              Collector
            </span>
            <span className={MPFonts.textNormalMedium}>
              {generateFormattedUserFullName(session.account.fullName)}
            </span>
          </div>
        </div>
      </div>
    </StandardDialog>
  );
}
