import DropVideoPlayer, { DropVideoPlayerProps } from './VideoPlayer';

import * as styles from 'css/pages/drops/components/VideoSection.module.css';

interface DropVideoSectionProps extends Omit<DropVideoPlayerProps, 'source'> {
  title: DropVideoPlayerProps['title'];
  source?: DropVideoPlayerProps['source'];
}

export default function DropVideoSection({
  source,
  ...props
}: DropVideoSectionProps) {
  return source ? (
    <div className={styles.videoSection}>
      <div className={styles.videoSectionAbs}>
        <DropVideoPlayer source={source} {...props} />
      </div>
    </div>
  ) : (
    <></>
  );
}
