import { useMemo } from 'react';
import pluralize from 'pluralize';

import {
  MPColorClass,
  MPFonts,
  MPTooltip,
  useIsMobile,
} from '@mp-frontend/core-components';
import { InfoIcon } from '@mp-frontend/core-components/icons';
import { joinClasses } from '@mp-frontend/core-utils';

import {
  DropSectionEnum,
  DropSubSectionEnum,
} from 'types/__generated__/graphql';

import CollectionCorePageCard from 'pages/collectionCore/CollectionCorePageCard';
import GenerativePageCard from 'pages/generative/GenerativePageCard';
import OpenEditionPageCard from 'pages/openEdition/OpenEditionPageCard';
import ProductPageCard from 'pages/product/card';
import CSSGlobal from 'types/enums/css/Global';
import { DropSectionSubSectionType, DropSectionType } from 'types/graphql/Drop';

import * as styles from 'css/pages/drops/components/sections/DropSection.module.css';

export const DROP_SECTION_TO_TITLE_MAPPING = {
  [DropSectionEnum.OneOfOne]: 'Unique',
  [DropSectionEnum.OpenEdition]: 'Timed Edition',
  [DropSectionEnum.LimitedEdition]: 'Limited Edition',
  [DropSectionEnum.Generative]: 'Generative Mint',
};

const DROP_SUB_SECTION_TO_TITLE_MAPPING = {
  [DropSubSectionEnum.AcceptingOffers]: 'Accepting Offers',
  [DropSubSectionEnum.CollectionDrop]: 'Collection Drop',
  [DropSubSectionEnum.FixedSale]: 'Fixed Price',
  [DropSubSectionEnum.RankedAuction]: 'Ranked Auctions',
  [DropSubSectionEnum.TimedAuctionOrReserve]: 'Timed Auctions',
};

const DROP_SUB_SECTION_TO_INFO_MAPPING = {
  [DropSubSectionEnum.AcceptingOffers]:
    'Make your best offer for the artist to accept.',
  [DropSubSectionEnum.CollectionDrop]:
    'Purchase to receive a random unique artwork from the collection.',
  [DropSubSectionEnum.FixedSale]: 'Buy-it-now at a set price.',
  [DropSubSectionEnum.RankedAuction]:
    'Compete to win an edition by bidding your way onto the leaderboard.',
  [DropSubSectionEnum.TimedAuctionOrReserve]:
    'Bid above a reserve price to kick off a timed, 24-hour auction.',
};

interface DropSectionProps {
  invalidate: () => void;
  sectionType: DropSectionType;
  subSectionsItems: DropSectionSubSectionType;
}

function DropSection({
  subSectionsItems,
  sectionType,
  invalidate,
}: DropSectionProps) {
  const isMobile = useIsMobile();
  const itemsCount = useMemo(
    () =>
      subSectionsItems.reduce(
        (count, subSection) => count + subSection.items.length,
        0
      ),
    [subSectionsItems]
  );

  return (
    <div
      data-section={sectionType}
      className={joinClasses(CSSGlobal.Cursor.Default, styles.dropSection)}
    >
      <div
        className={joinClasses(
          {
            [MPFonts.headline2]: !isMobile,
            [MPFonts.headline3]: isMobile,
          },
          styles.dropSectionTitle
        )}
      >
        {pluralize(DROP_SECTION_TO_TITLE_MAPPING[sectionType], itemsCount)}
      </div>
      {subSectionsItems.map((subSection, idx) => (
        <div
          className={joinClasses({
            [CSSGlobal.Flex.CenteredCol]: !isMobile,
            [CSSGlobal.Flex.Col]: isMobile,
          })}
          key={subSection.subSectionType}
        >
          <div
            className={joinClasses(
              {
                [MPFonts.headline3]: !isMobile,
                [MPFonts.headline4]: isMobile,
                [styles.additional]: idx !== 0,
              },
              styles.dropSubSectionTitle
            )}
          >
            {DROP_SUB_SECTION_TO_TITLE_MAPPING[subSection.subSectionType]}&nbsp;
            <MPTooltip
              title={
                DROP_SUB_SECTION_TO_INFO_MAPPING[subSection.subSectionType]
              }
              placement="right"
            >
              <InfoIcon
                className={MPColorClass.SolidNeutralGray3}
                fontSize="16"
              />
            </MPTooltip>
          </div>

          <div className={styles.dropSubSectionItems}>
            {subSection.items.map((item) => (
              <div
                key={item.id}
                className={joinClasses(styles.dropCardContainer)}
              >
                {sectionType === DropSectionEnum.Generative ? (
                  <GenerativePageCard
                    contractQuery={{
                      variables: { address: item.contract.address },
                    }}
                    nftMetadata={item}
                  />
                ) : sectionType === DropSectionEnum.OpenEdition ? (
                  <OpenEditionPageCard
                    nftMetadata={item}
                    invalidate={invalidate}
                  />
                ) : subSection.subSectionType ===
                  DropSubSectionEnum.CollectionDrop ? (
                  <CollectionCorePageCard
                    nftMetadata={item}
                    invalidate={invalidate}
                  />
                ) : (
                  <ProductPageCard nftMetadata={item} invalidate={invalidate} />
                )}
              </div>
            ))}
          </div>
        </div>
      ))}
    </div>
  );
}

export default DropSection;
